body{
  background-color: #333;
  color:white ;
}

.App {
  text-align: center;
}

.taskBg {
   background-color: #555;
   border-radius: 6px;
   margin-bottom: 20px;
   padding: 10px 140px 10px 15px;
   text-align: left;
   font-weight: 22px;
   position: relative;
}

.taskBg div {
  display: flex;
  align-items: center;
}

.teskNumber {
  color: #999;
  border: 1px solid #999;
  border-radius: 50%;
  width: 26px;
  height: 26px;
  display: flex;
  text-align: center;
  font-size: 14px;
  margin-right: 8px;
  align-items: center;
  justify-content: center;
}

.taskBg .teskText {
  flex: 1;
}
.taskBg .done .teskText {
  text-decoration: line-through;
  opacity: 0.3;
  flex: 1;
}

.iconsWrap {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 15px;
  width: auto;
  display: inline-block;
}

.iconsWrap span {
  display: inline-block;
  margin: 0px 10px 0 10px;
  cursor: pointer;
  color: #00ff89;
}
.iconsWrap span:hover{
  color: red;
}

.mr-20{
  margin-right: 20px;
}